.product-detail
	.button-wrapper
		.btn
			min-width: 160px
			
			&:not(:last-child)
				@apply mr-2
			@media screen and ( max-width:767px)
				@apply mb-2
	.row-padding
		@apply py-12
		@screen lg
			@apply pt-20 pb-28
		h1
			@apply text-32 text-grey-2 uppercase font-bold mb-3
		.caption
			@apply text-28 font-light text-grey-2
			*
				@apply text-28 font-light text-grey-2 leading-snug
			@media screen and ( max-width:1024px)
				@apply 	mb-8
				
		.button-wrapper
			@apply mt-3
				
			.btn
				@media screen and ( max-width: 767px)
					@apply mx-0
					&:first-child
						@apply mr-2
				&:first-child
					@apply ml-0
	.product-image
		.wrapper
			display: block
			height: 0
			overflow: hidden
			padding-top: 280 / 530 * 100%
			position: relative
			img
				position: absolute
				top: 0
				left: 0
				width: 100%
				height: 100%
				object-fit: contain
.product-detail-image
	@apply  bg-azure-4 pt-12 pb-16 
	@screen lg
		@apply pt-20 pb-30
	.swiper-product-detail
		position: relative
		.nav-arrow-prev,.nav-arrow-next
			padding: 5px 15px
			transition: .3s all ease-in-out
			@media screen and ( min-width: 1024px )
				&:hover
					background: rgba(#333333 ,.8 )
					span
						@apply text-white
			span
				@apply transition
				height: 25px
				color: #959ea7
				width: 24px
		.nav-arrow-prev
			left: -70px
			@media screen and ( min-width:1200px) and ( max-width:1250px)
				left: -35px
			@media screen and ( max-width:1050px)
				left: -35px
			@media screen and ( max-width:  1024px)
				left: 0
		.nav-arrow-next
			right: -70px
			@media screen and ( min-width:1200px) and ( max-width:1250px)
				right: -35px
			@media screen and ( max-width:1050px)
				right: -35px
			@media screen and ( max-width:  1024px)
				right: 0
		.wrapper
			@apply p-10 bg-white
			@screen lg
				@apply px-16 
			a
				+img-ratio(620/1100,contain)
			
	.swiper-pagination
		@apply left-50 translate-x-50
		bottom: -40px
		display: block !important
.product-detail-summary
	@apply  bg-white pt-12 pb-6
	@screen lg
		@apply pt-16 pb-12
	.caption-text
		ul
			list-style-type: disc
			@apply pl-8
			li
				@apply mb-0
				&:hover
					@apply underline
					
		
		.title,strong
			@apply text-16 text-grey-2 text-left mb-2 font-bold inline-block mb-4
		strong
			margin-bottom: 0 !important
		@apply text-14 text-grey-2 text-left leading-relaxed mb-4
		*
			@apply text-14 text-grey-2 text-left leading-relaxed mb-4

			
.product-detail-option
	@apply bg-azure-4 py-12
	@screen lg
		@apply pt-16 pb-20
	.row-wrapper-cards
		@apply -mx-4 
		display: flex
		flex-wrap: wrap
		.column 
			-ms-flex: 50%
			flex: 50%
			max-width: 50%
			padding: 0 15px
			@media screen and (max-width: 600px) 
				-ms-flex: 100%
				flex: 100%
				max-width: 100%

	.cards
		@apply bg-white p-5 mb-5 inline-block
		vertical-align: top
		width: 100%
		.title
			@apply text-14 text-grey-2 font-bold text-left inline-block mb-3
		.caption
			@apply text-14 text-grey-2 text-left leading-relaxed
			*
				@apply text-14 text-grey-2 text-left leading-relaxed mb-4
.product-detail-specification
	@apply bg-white py-12 
	@screen lg
		@apply pt-12 pb-16
	.container
		@apply relative
	.button-wrapper-right
		@apply mt-4
		@media screen and ( min-width: 1024.1px )
			@apply  mb-8 absolute
			right: 15px
			top: 0
	article
		*
			@apply leading-relaxed
	table
		@apply w-full
		border: none
		tbody
			tr
				border: none
				&:not(:last-child)
					border-bottom: 1px solid #f1f9fb
				td
					border: none
					min-height: 50px
					padding: 15px 0
					*
						@apply leading-relaxed
					ul
						list-style-type: disc
						padding: 0 0 0 20px
						li
							&:hover
								@apply underline
					&:first-child
						width: 100%
						@apply text-14 font-bold text-grey-2 inline-block
						*
							@apply text-14 font-bold text-grey-2 inline-block
						&::first-letter
							@apply uppercase
					&:nth-child(2)
						width: 66.666667%
						@apply text-14 font-normal text-grey-2
						*
							@apply text-14 text-grey-2
	.toggle-wrapper
		@apply mt-8
		@media screen and ( min-width: 1024.1px )
			@apply mt-20
	.toggle-item
		&.active
			.title
				span
					@apply text-dove
		.title
			@apply py-2
			min-height: 55px
			
		article
			@apply py-0
			p
				@apply mb-0
.product-detail-document
	@apply bg-azure-4 pt-12 pb-0
	@screen lg
		@apply pt-20 pb-8
	ul
		list-style-type: disc
		padding-left: 20px
		li
			&:hover
				@apply underline
	.document-image
		height: 70px
		width: auto
		@apply mb-6
		img
			width: 100%
			height: 100%
			object-fit: scale-down
			@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none)
				width: auto
				@apply block mx-auto

	
	.figure-document
		@apply mb-10
		.title
			@apply text-24 text-grey-2 text-left pb-4 mb-4
			border-bottom: 1px solid $grey-2
		.caption
			@apply text-14 text-left leading-normal text-primary-1 mb-1
			*
				@apply text-14 text-left leading-normal text-primary-1 mb-1
	.wrapper
		@apply flex items-center justify-start pt-2
		flex-wrap: wrap
	.document-item
		@apply flex flex-col mb-4 mx-4
		max-width: 160px
		.figure-image
			@apply flex items-center justify-center
			background: #eaeef3
			width: 160px
			height: 160px
			a
				height: 70px
				width: auto
				img
					width: 100%
					height: 100%
					object-fit: scale-down
		@media screen and ( max-width:1024px)
			max-width: 110px
			.figure-image
				width: 110px
				height: 110px
				a
					height: 50px
			
		.text-title
			@apply mt-4 text-24 text-grey-2
			*
				@apply mt-4 text-24 text-grey-2
			@media screen and ( max-width:  1024px)
				@apply text-20
				*
					@apply text-20
				
.product-detail-faq
	@apply bg-white pt-12
	@screen lg
		@apply py-20
	.toggle-item
		.title
			height: 55px
		
.product-detail-accessory
	@apply bg-white pt-12 pb-20
	@screen lg
		@apply pb-20
	.swiper-product-accessory
		@apply relative
		.swiper-pagination
			@apply hidden
			@media screen and ( max-width: 1024px)
				@apply block
				
	.figure-accessory
		.figure-image
			@apply px-4
			border: 1px solid #e0f0f5
			a
				+img-ratio(140/200,contain)
		.figcaption
			.title
				@apply text-14 font-bold text-primary-1 uppercase mt-4 inline-block
				display: -webkit-box
				-webkit-line-clamp: 2
				-webkit-box-orient: vertical
				overflow: hidden
				height: 2rem
			.caption
				@apply text-12 text-grey-2 font-light text-left mt-3
				display: -webkit-box
				-webkit-line-clamp: 3
				-webkit-box-orient: vertical
				overflow: hidden
				height: 3.375rem
				*
					@apply text-12 text-grey-2 font-light text-left
.product-detail-news
	@apply bg-azure-4 pt-12 pb-20
	@screen lg
		@apply pt-20 pb-16
	.swiper-news-related
		@apply relative
	.swiper-pagination
		@apply hidden
		@media screen and ( max-width:1024px)
			@apply block
	.product-news-related
		.figure-image
			a
				+img-ratio(220/220,cover)
		.figcaption
			@apply mt-4
			@screen lg
				@apply mt-6
			.title
				a
					@apply text-16 text-grey-2 text-left font-bold leading-normal
					display: -webkit-box
					-webkit-line-clamp: 3
					-webkit-box-orient: vertical
					overflow: hidden
					height: 4.5rem
			.caption
				@apply mt-4 text-12 font-light
				display: -webkit-box
				-webkit-line-clamp: 3
				-webkit-box-orient: vertical
				overflow: hidden
				height: 3.375rem
				*
					@apply  text-12 font-light

				
.product-detail-nav
	&.scroll-to-fixed-fixed
		@apply pt-6
		.row
			@apply flex opacity-100
			max-height: 50px
			@media screen and ( max-width: 767px)
				max-height: 100%
		@media screen and ( max-width:576px)
			@apply pb-2
	&.detail-nav-2
		@apply pb-0
		ul
			li
				a
					height: 50px
		
		.head-title
			@apply mb-0 pb-0
	.button-wrapper-right
		@media screen and ( max-width:767px)
			margin: 0 -10px
			@apply flex flex-row justify-between items-center 
			flex-wrap: wrap
			.btn
				padding: 0 10px
				width: calc( 50% - 20px )
				&:not(:last-child)
					@apply mb-4
				@media screen and ( max-width:1024px)
					@apply mb-2
	.btn-primary
		height: 40px
		span
			@apply text-14
			line-height: 1
			
	.button-wrapper
		@media screen and ( min-width:767.9px) and ( max-width:1024px)
			@apply flex flex-row items-center justify-end 
			flex-wrap: wrap
			flex: 0 0 100%
			max-width: 100%
			.btn
				@apply mt-0
				flex: 0 0 calc(50% - 30px)
				max-width: calc(50% - 30px)
				&:last-child
					@apply mt-4
		@media screen and ( max-width:767.9px)
			@apply whitespace-no-wrap
			.btn
	
				&:first-child
					@apply ml-0

			
	.row
		@apply  opacity-0 transition flex pt-0
		max-height: 0
		@media screen and ( max-width: 1024px)
			@apply pt-0
			
		.head-title
			@apply text-24 font-normal uppercase
			@media screen and ( max-width: 767.9px)
				@apply mb-0
		.col
			&:nth-child(2)
				@media screen and ( max-width:  767px)
					@apply overflow-x-auto
					
	@apply bg-primary-1
	*
		@apply text-white font-bold text-16
	@media screen and ( max-width: 767px )
		white-space: nowrap
		ul
			@apply overflow-auto justify-start
		
	ul
		@apply flex
		li
			@apply relative
			&:not(:last-child)
				@apply inline-block pr-10
			&.active
				a
					transition: .3s all  ease-in-out
					color: #31b6ee
					&::before
						opacity: 1
			a
				@apply flex items-center justify-center relative
				height: 70px
				@media screen and ( max-width:1439px)
					height: 50px
				&:before
					top: 100%
					left: 50%
					border: solid transparent
					content: " "
					height: 0
					width: 0
					position: absolute
					pointer-events: none
					border-color: rgba(44, 62, 80, 0)
					border-top-color: #2c3e50
					border-width: 12px
					opacity: 0
					margin-left: -12px
					transition: .3s all ease-in-out
				&:hover
					@apply text-white
