.product-news-section-wrapper
	.product-section-zone
		&:nth-child(even)
			.row
				@apply flex-row-reverse
	.caption
		padding: 0 !important
.product-section-zone
	@apply py-12
	@screen lg
		@apply py-32
	&.reserve-zone
		.row
			@apply flex-row-reverse
	&.grey-text
		.head-title
			@apply text-grey-2
		.caption
			@apply text-grey-2
			*
				@apply text-grey-2
		.btn-view-more
			span
				@apply text-blue
	.btn-view-more
		span
			@apply text-yellow
		@media screen and ( max-width:767.9px)
			@apply items-center justify-center
			
	.figure-caption
		.head-title
			@apply text-white
		.btn
			@apply mt-6
		@media screen and ( max-width:767.9px)
			@apply text-center
			
	.caption
		@apply text-14 leading-relaxed text-left text-white pl-0
		@screen lg
			@apply pl-4
		*
			@apply text-14 leading-relaxed text-left text-white
		@media screen and ( max-width:767.9px)
			@apply text-center px-8
		@media screen and ( max-width:450px)
			@apply px-4
			
	.image-wrapper
		@media screen and ( max-width:767.9px)
			@apply mt-10
		a
			@media screen and ( max-width:767.9px )
				height: 200px
				width: auto
				img
					max-width: 100%
					height: auto
					object-fit: contain
					@apply mx-auto block
				// +img-ratio(330/530,contain)
				// img
				// 	@apply block top-50 left-50 translate-50 h-auto