.primary-banner
	.wrapper
		.banner-image
			a
				@apply block w-auto w-full h-full
				min-height: calc( 100vh - 60px )
				object-fit: cover
				background-position: center center
				background-repeat: no-repeat
				background-size: cover
				@media screen and ( min-width: 1024.1px )
					min-height: calc( 100vh - 100px )
	.container
		@apply absolute left-50 translate-x-50
		top: 80px
		@media screen and ( max-width: 1200px)
			top: 50px
		@media screen and ( max-width: 767px)
			@apply top-50 translate-50
			
		.box-content
			padding: 50px 30px
			background-color: rgba($main-1,.8)
			opacity: 0.8
			max-width: 570px
			@media screen and ( max-width:1200px)
				padding: 30px 
				
			@media screen and ( max-width: 1024px)
				max-width: 320px
			@media screen and ( max-width: 576px)
				max-width: 100%
			h2
				@apply text-white text-42 leading-normal mb-4
				@media screen and ( max-width: 1440px)
					@apply text-36 
				@media screen and ( max-width: 1024px)
					@apply text-32 leading-tight
			.caption
				@apply mb-6 text-20 text-left text-white
				p
					@apply  text-20 text-left text-white
		@media screen and ( max-width: 1024px)
			max-width: 100%
			.box-content
				max-width: 100%
				padding: 30px
	.btn
		border: 1px solid #ffffff
		@apply m-0
		span
			@apply text-white
		@media screen and ( max-width:576px)
			min-width: 100%
			