.search-page
	padding: 70px 0
	h1
		letter-spacing: -2px
		margin-bottom: 20px
		font-size: 45px
		line-height: 55px
		font-weight: 500
		color: #333333
		@media (max-width: 992px)
			font-size: 24px
			line-height: 30px
		&.center
			text-align: center
	.btn-reindex
		display: none
	.searchcontrols
		.form-inline
			position: relative
			&:before
				position: absolute
				content: '\F0349'
				font-family: "Material Design Icons"
				top: 0
				right: 10px
				z-index: 1
				font-size: 24px
				color: #4285f4
				width: 44px
				height: 44px
				display: flex
				align-items: center
				justify-content: center
		input[type="submit"]
			position: absolute
			top: 0
			right: 10px
			z-index: 2
	input[type="text"]
		width: 100%
		height: 100%
		padding-left: 20px
		padding-right: 54px
		border: 1px solid #dfe1e5
		background-color: #fff
		color: #333
		font-size: .875rem
		font-weight: 400
		height: 44px
		border-radius: 22px
		box-shadow: 0 0 0 0px rgba(0,0,0,0)
		transition: .25s cubic-bezier(.4,0,.2,1)
		outline: none
		&:focus
			box-shadow: 0 1px 6px 0 rgba(32,33,36,0.28)
			border-color: rgba(223,225,229,0)
	input[type="submit"]
		border-radius: 2px
		user-select: none
		background-color: rgba(0,0,0,0)
		border: 0
		box-shadow: none
		cursor: pointer
		text-align: center
		display: flex
		align-items: center
		justify-content: center
		height: 40px
		outline: none
		width: 44px
		height: 44px
		border-radius: 50%
		font-size: 0
	.text-danger
		margin-top: 20px
		font-size: 15px
		color: #333
		font-style: italic
		font-weight: 600
	.searchresultsummary
		margin-bottom: 20px
	.searchresults
		margin-top: 30px
		.modulepager
			&:first-child
				display: none
	.searchresultlist
		margin-bottom: 30px
	.searchresult
		margin-bottom: 30px
		box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28)
		padding: 16px
		border-radius: 8px
		&:last-child
			margin-bottom: 0
		h3
			font-size: 18px
			line-height: 1.33
			font-weight: 400
			margin-bottom: 10px
			a
				color: #1A0DAB
				text-decoration: none
				font-weight: 500
				&:hover
					text-decoration: underline
		.searchresultdesc
			color: #545454
			line-height: 1.54
			word-wrap: break-word
			font-size: small
			font-weight: 400
			margin-bottom: 10px
		.searchterm
			color: #6a6a6a
			font-weight: bold
			font-style: normal
			line-height: 1.54
			word-wrap: break-word
			font-size: small
		hr
			display: none !important

.searchbox
	bottom: -1px
	z-index: 10
	@apply right-0  opacity-0 overflow-visible absolute block h-0 w-full
	transition: opacity .1s linear 0s,left 0s linear .1s
	@media screen and ( max-width:1024px)
		@apply translate-0

		
	&.active
		@apply opacity-100
		z-index: 100
		input
			opacity: 1
			pointer-events: auto
			z-index: 1
		input
			cursor: auto
		button
			cursor: pointer
	input
		border: 1px solid #f1f1f1
		padding: 0 16px
		position: absolute
		top: 0
		height: 31px
		right: 0
		opacity: 0
		cursor: none
		pointer-events: none
		z-index: -1
		transition: opacity .3s ease-in-out
		font-size: 16px
		color: #333333
		@media screen and ( max-width:  576px)
			@apply w-full
			
		&:focus
			outline: none
			transition: none
		&::placeholder
			font-size: 13px
	
			
	button
		color: #ffffff
		background: $gray
		width: 32px
		height: 30px
		opacity: 1
		pointer-events: auto
		z-index: 1
		cursor: pointer
		border: none
		pointer-events: auto
		position: absolute
		top: 0
		right: 0
		transition: all .3s ease-in-out
		em
			color: #ffffff
		&:focus
			outline: none
.search-wrapper
	@apply static h-full
	@screen sm
		@apply relative
	.search-toggle
		@apply flex items-center justify-center uppercase  text-12 transition relative  h-full
		width: 35px
		z-index: 999
		em
			@apply inline-block text-white transition pointer-events-none
			font-size: 18px
	@media screen and(max-width:1025px)
		@apply flex items-center justify-center
